
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'

import {mapGetters} from 'vuex'
export default {
props:['type'],
data(){
return {
title:'地址',
ipOrPort:true,
value:''
}
},
mounted(){
if(this.type.ip){
this.ipOrPort = true;
this.value = this.type.ip;
this.title = "地址"
}else{
this.ipOrPort = false;
this.value = this.type.port;
this.title = "端口"
}
},
components: {
XTextarea,
XInput
},
computed: {
...mapGetters([
'ip',
'port'
])
},
methods:{
save(){
if(this.ipOrPort){
this.$store.commit('SET_IP',this.value);
this.$store.commit('SET_URL',`${this.value}:${this.port}`);
}else{
this.$store.commit('SET_PORT',this.value);
this.$store.commit('SET_URL',`${this.ip}:${this.value}`);
}
this.$router.go(-1)
}
}
}



