import loadVue from './load.vue'

export default {
  data(){
    return {
      msgState:0,
      load:false,
      msgOrNet:true,
      rest:null
    }
  },
  components: {
    loadVue
  },
  watch:{
    msgState(val){
      if(val !== 0){
        if(val == 1){
          this.msgOrNet = true;
        }else{
          this.msgOrNet = false;
        }
        this.load = true;
      }else{
        this.load = false;
      }
    }
  },
  computed:{
    text(){
      return this.msgOrNet?this.$t('暂无记录'):this.$t('暂无网络')
    }
  },
  methods:{
    click(){
      this.rest && this.rest();
    }
  }
}



