
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import {mapGetters} from 'vuex'

export default {
data(){
return {
}
},
mounted(){
},
components: {
Actionsheet
},
computed: {
...mapGetters([
'lan',
'ip',
'port'
]),
locale(){
if (/en/.test(this.lan)) {
return 'English'
}
return '中文'
}
},
methods: {
red(){
this.$store.commit('SET_URL', `${process.env.BASE_API}:${process.env.BASE_PORT}`);
this.$store.commit('SET_IP', `${process.env.BASE_API}`);
this.$store.commit('SET_PORT', `${process.env.BASE_PORT}`);
this.$vux.toast.text(this.$t('还原成功'), 'bottom')
}
}
}



