export default {
  props: ['typeStyle', 'mini'],
  computed: {
    line(){
      return !this.typeStyle ? 'line-scale-pulse-out' : this.typeStyle
    },
    min(){
      return (this.mini === undefined) ? '' : 'line-mini'
    }
  }
}



