
import {plusready, plusOpen, plusToast} from 'common/plus'
import XButton from 'vux/src/components/x-button/index.vue'
import Divider from 'vux/src/components/divider/index.vue'

import Storage from 'common/data/storage'
import {login} from 'api/login'
import sql from 'common/data/websql'
import Cache from 'common/data/clearCache'
import md5 from 'js-md5';

export default {
created(){


plusready(this.plusready);
},
components: {
XButton,
Divider
},
data(){
return {
loginState: true,
loginPassState: true,
cw: '',
iconType: '',
username: '',
password: '',

err: '',
errPass: '',
getui: '',
sql: null,
disabled: true,
loading: false,
plusViedo: null
}
},
mounted(){
if (this.$route.query.phone) {
this.username = this.$route.query.phone;
}
document.onkeydown = (event) => {
if (event.keyCode == 13) {
this.login();
}
};
var old_back = mui.back;
mui.back = function () {
mui.confirm(this.$t("手滑了一下，要退出吗") + '?', '', function (e) {
if (e.index == 1) {
plus.runtime.quit();
}
});
}
},
watch: {
username(val){
if (val !== "" && this.password.length >= 6) {
this.disabled = false;
return;
}
this.disabled = true;
},
password(val){
if (val.length >= 6 && (this.username !== "")) {
this.disabled = false;
return;
}
this.disabled = true;
}
},
computed: {},
methods: {
plusready(){
this.cw = plus.webview.currentWebview();
let plusGetui = plus.push.getClientInfo();
this.getui = plusGetui.clientid;
new Cache();
},
check(){
if (this.username === "") {
this.loginState = false;
this.err = this.$t('用户名不能为空');
return false
}
if (this.password.length < 6) {
this.loginPassState = false;
this.errPass = this.$t('密码不能小于6位');
return false
}
return true
},
login(){
if (!this.check()) return;
let data = {
userAccount: this.username,
userPwd: md5(this.password),
userCid: this.getui,
osType: mui.os.ios ? 2 : 1
};
this.loading = true;
login(data).then(res => {
if (res.resultStatus) {
if (res.data.isPhoneCheck === 1) {
this.phoneCheck();
this.loading = false;
} else {
this.$emit('viewshow');
}
new Storage().getUserInfo(res.data);
} else {
this.loginState = false;
this.err = res.msg;
this.loading = false;
}
}).catch(err => {
plusToast(this.$t("登录超时，请先检查网络"));
this.loading = false;
});
},
phoneCheck(){
this.$router.push({name: 'phoneVer'});
},
setting(){
this.$router.push({name: 'server'})
},
blur(){
this.loginState = true;
this.loginPassState = true;
},
forget(){
this.$router.push({name: 'goVer', query: {type: 'forget'}});
},
Apply(){
this.$router.push({name: 'Apply'});
}
}
}



